'use strict'

// import Cookies from 'js-cookie'
// import moment from 'moment'
// import clone from 'clone'
// import Toastr from '../component/common/toastr/index'

/**
 * tools.formatMoney(12346)      //12,346.00
 * tools.formatMoney(12346,0)    //12,346
 *
 * @param {any} num
 * @param {any} places 
 * @returns number
 */
function haveSomethings(param) {
    if (typeof param === 'string') {
        return Boolean(param)
    }

    if (param instanceof Array) {
        return Boolean(param.length)
    }

    if (typeof param === 'object') {
        for (let k in param) {
            if (param.hasOwnProperty(k)) {
                return true
            }
        }
        return false
    }

    if (typeof param === 'undefined') {
        return false
    }

    return Boolean(param)
}
export let haveSomething = haveSomethings

export const emptyObj = function (obj) {
    for (let k in obj) {
        if (obj.hasOwnProperty(k)) {
            return false
        }
    }
    return true
}
export const goHead = function (forceGoHead) {
    if (forceGoHead === 'forceGoHead') {
        return window.history.go(-1)
    }

    if (window.history.length > 2) {
        return window.history.go(-1)
    }

    window.location.href = '/'
}

export const dataArrSort = function (dataArr, sortKey) {
    sortKey = sortKey ? sortKey : 'display_rank'
    if (haveSomethings(dataArr)) {
        dataArr.sort(function (item_a, item_b) {
            if (item_a[sortKey] > item_b[sortKey] ) {
                return 1
            } else {
                return -1
            }
        })
    } else {
        dataArr = []
    }
    return dataArr
}
export const getUrlParam = function (key) {
    let local_url = document.location.href
    let params_str = local_url.split('?').length > 1 ? local_url.split('?').pop() : ''
    let hash = window.location.hash
    params_str = params_str.replace(hash, '')
    let params_arr = params_str.split('&')
    let params_obj = {}
    for (let param_index = 0; param_index < params_arr.length; param_index++) {
        let param_kv_arr = params_arr[param_index].split('=')
        if (param_kv_arr.length > 1) {
            params_obj[decodeURIComponent(param_kv_arr[0])] = decodeURIComponent(param_kv_arr[1])
        }
    }
    return params_obj[key] === undefined ? '' : params_obj[key]
}
export const isMobile = function () {
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)
}
export const rotateMagic = function (rotate) {
    let a = 0

    try {
        a = Number(rotate) % 360
    } catch (e) {
        return 0
    }

    if (a >= 180 && a <= 360) {
        return a - 360
    }

    if (a >= -180 && a <= 180) {
        return a
    }

    if (a >= -360 && a <= -180) {
        return 360 + a
    }

    return 0
}


