import Vue from 'vue'
import Router from 'vue-router'
import HelloEveryDay from '@/components/HelloEveryDay'
import Upload from '@/components/Upload'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloEveryDay',
      component: HelloEveryDay
    },
    {
      path: '/upload',
      name: 'Upload',
      component: Upload
    }
  ]
})



