<template>
	<div>
    <div class="file-wrap" v-if="!uploadState">
      <div class="icon-wrap">
        <i class="fa fa-plus-circle"></i>
        <div class="file-name">{{file.name?file.name:''}}</div>
      </div>
      <input type="file" name="file" id="file" @change="handleChange($event)" accept="image/*">
    </div>
    <div v-else>
      <Create></Create>
    </div>
  </div>
</template>

<script>
import Create from './create/index'
import getUploadToken from "../assets/js/getUploadToken";
import * as qiniu from "qiniu-js";
export default {
  name: "Upload",
  data() {
    return {
      file:'',
      uploadState: false,
      uploadImgSrc: ''
    };
  },
  components:{
    Create
  },
  methods: {
    handleChange(e) {
      this.$store.dispatch("loading/showLoading");
      this.file = e.target.files[0]
      let key = this.file.name;
      let token = getUploadToken();
      var observable = qiniu.upload(this.file, key, token);
      var subscription = observable.subscribe(
        res => {
          console.log(res);
        },
        err => {
          this.$store.dispatch("loading/hideLoading");
          console.log(err);
        },
        res => {
          this.$store.dispatch("loading/hideLoading");
          this.uploadState = true
          this.uploadImgSrc = 'http://p99rv22ms.bkt.clouddn.com/' + res.key
          console.log(res);
        }
      );
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.file-wrap {
  .icon-wrap {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    font-size: .5rem;
    line-height: 0;
    .file-name {
      font-size: .16rem;
      line-height: 1;
    }
  }
  input {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    opacity: 0;
    width: .5rem;
    height: .5rem;
  }
}
</style>



