<template>
	<div class="every-day-text">
		<div @click="getText">
			{{msg}}
		</div>
	</div>
</template>

<script>
export default {
  name: "HelloEveryDay",
  components: {},
  mounted() {
    this.getText()
    // this.getImage()
  },
  methods:{
    getText() {
      this.$http.get("https://api.imjad.cn/hitokoto/?cat=e&charset=gbk&encode=json")
      .then(
        (res) => {
          clearInterval(this.timer)
          this.msg = ''
          this.result = res.data.hitokoto
          this.setTextTimer()
        },
        (res) => {

				}
      );
    },
    setTextTimer(){
      let _this = this
      this.timer = setInterval(function(){
        if(_this.msg.length == _this.result.length){
          clearInterval(_this.timer)
          return
        }
        _this.msg += _this.result[_this.msg.length]
      },200)
    },
    getImage() {
      this.$http.get("https://www.xwboke.cn/api/api.php")
      .then(
        (res) => {
          console.log(res)
        },
        (res) => {

				}
      );
    }
  },
  data() {
    return {
      msg: "",
      result:'',
      timer:null,
    };
  }
};
</script>

<style scoped>
  .every-day-text {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
</style>



