<template>
  <div id="app">
    <div class='nav-wrap'>
      <div class="mask" v-show="navState"></div>
      <div :class="navState?'content-wrap is-active':'content-wrap'">
        <div class="hamburger" @click='toggleNavState'>
          <span class="line"></span>
          <span class="line"></span>
          <span class="line"></span>
        </div>
        <ul class="nav-list">
          <router-link tag="li" to="/" @click.native="toggleNavState">
            <a></a>
          </router-link>
          <router-link tag="li" to="/upload"  @click.native="toggleNavState">
            <a></a>
          </router-link>
        </ul>
      </div>
    </div>
    <loading v-show="getLoadingState"></loading>
    <router-view/>
  </div>
</template>

<script>
import Loading from "@/components/Loading"
import CircleMenu from "vue-circle-menu"
export default {
  name: "App",
  data() {
    return {
      arr: [1, 2, 3, 4, 5],
      navState: false,
    };
  },
  components: {
    Loading,
    CircleMenu
  },
  computed:{
    getLoadingState () {
      return this.$store.state.loading.loadingState
    },
  },
  methods: {
    toggleNavState() {
      this.navState = !this.navState;
    },
  }
};
</script>

<style lang='less'>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size: 0.16rem;
  // background: url('http://p99rv22ms.bkt.clouddn.com/default.jpg') no-repeat center;
  // background-size: cover;
  // min-height: 100vh;  
  .nav-wrap {
    position: fixed;
    right: .2rem;
    bottom: .2rem;
    .mask {
      background-color: rgba(0, 0, 0, 0.6);
      position: fixed;
      z-index: 10;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
    }
    .content-wrap {
      position: relative;
      z-index: 11;  
      .hamburger {
        position: relative;
        z-index: 15;
        transform: scale(.7);
        .line {
          width: .5rem;
          height: .05rem;
          background-color: #000;
          display: block;
          margin: .08rem auto;
          -webkit-transition: all 0.3s ease-in-out;
          -o-transition: all 0.3s ease-in-out;
          transition: all 0.3s ease-in-out;
        }
      }
      .nav-list {

        li {
          display: inline-block;
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          right: 0;
          opacity: 0;
          transition: .2s .1s;
          a {
            color: #fff;
            display: inline-block;
            width: 100%;
            line-height: .3rem;
          }
        }
      }
      &.is-active .nav-list li:nth-child(1) {
        opacity: 1;
        top: -1rem;
      }
      &.is-active .nav-list li:nth-child(2) {
        opacity: 1;
        top: -.5rem;
      }
      &.is-active .hamburger .line:nth-child(1) {
        -webkit-transform: translateY(.13rem) rotate(45deg);
        -ms-transform: translateY(.13rem) rotate(45deg);
        -o-transform: translateY(.13rem) rotate(45deg);
        transform: translateY(.13rem) rotate(45deg);
      }
      &.is-active .hamburger .line:nth-child(2) {
        opacity: 0;
      }
      &.is-active .hamburger .line:nth-child(3) {
        -webkit-transform: translateY(-.13rem) rotate(-45deg);
        -ms-transform: translateY(-.13rem) rotate(-45deg);
        -o-transform: translateY(-.13rem) rotate(-45deg);
        transform: translateY(-.13rem) rotate(-45deg);
      }
    }
  }
}
</style>



