<template>
    <div ref="hammer" :class="classname" style="display: inline-block">
        <slot></slot>
    </div>
</template>

<script>
    import Hammer from 'hammerjs'

    export default {
        props: [
            'classname',
            'tap',
            'press',
            'pressup',
            'panstart',
            'panmove',
            'panend',
            'panleft',
            'panright',
            'swipeleft',
            'swiperight',
            'pinchstart',
            'pinchmove',
            'pinchend',
            'rotatestart',
            'rotatemove',
            'rotateend',
        ],
        mounted() {
            if (this.tap) this.tapFn()
            if (this.press) this.pressFn()
            if (this.pressup) this.pressupFn()

            if (this.panstart) this.panstartFn()
            if (this.panmove) this.panmoveFn()
            if (this.panend) this.panendFn()

            if (this.panleft) this.panleftFn()
            if (this.panright) this.panrightFn()

            if (this.swipeleft) this.swipeleftFn()
            if (this.swiperight) this.swiperightFn()

            if (this.pinchstart) this.pinchRotateFn()
            if (this.pinchmove) this.pinchRotateFn()
            if (this.pinchend) this.pinchRotateFn()

            if (this.rotatestart) this.pinchRotateFn()
            if (this.rotatemove) this.pinchRotateFn()
            if (this.rotateend) this.pinchRotateFn()
        },
        methods: {
            tapFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage)
                let Tap = new Hammer.Tap({interval: 100})

                mc.add(Tap)
                mc.on('tap', function (e) {
                    _this.tap(e)
                })
            },
            pressFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage)
                let Press = new Hammer.Press()

                mc.add(Press)
                mc.on('press', function (e) {
                    _this.press(e)
                })
            },
            pressupFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage)
                let Press = new Hammer.Press()

                mc.add(Press)
                mc.on('pressup', function (e) {
                    _this.pressup(e)
                })
            },
            panstartFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage, {
                    touchAction: 'pan-y',
                })
                let Pan = new Hammer.Pan()

                mc.add(Pan)
                mc.on('panstart', function (e) {
                    _this.panstart(e)
                })
            },
            panmoveFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage, {
                    touchAction: 'pan-y',
                })
                let Pan = new Hammer.Pan()

                mc.add(Pan)
                mc.on('panmove', function (e) {
                    _this.panmove(e)
                })
            },
            panendFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage, {
                    touchAction: 'pan-y',
                })
                let Pan = new Hammer.Pan()

                mc.add(Pan)
                mc.on('panend', function (e) {
                    _this.panend(e)
                })
            },
            panleftFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage, {
                    touchAction: 'pan-y',
                })
                let Pan = new Hammer.Pan()

                mc.add(Pan)
                mc.on('panleft', function (e) {
                    _this.panleft(e)
                })
            },
            panrightFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage, {
                    touchAction: 'pan-y',
                })
                let Pan = new Hammer.Pan()

                mc.add(Pan)
                mc.on('panright', function (e) {
                    _this.panright(e)
                })
            },
            swipeleftFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage, {
                    touchAction: 'pan-y',
                })
                let Swipe = new Hammer.Swipe()

                mc.add(Swipe)
                mc.on('swipeleft', function (e) {
                    _this.swipeleft(e)
                })
            },
            swiperightFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage, {
                    touchAction: 'pan-y',
                })
                let Swipe = new Hammer.Swipe()

                mc.add(Swipe)
                mc.on('swiperight', function (e) {
                    _this.swiperight(e)
                })
            },
            pinchRotateFn() {
                let _this = this
                let stage = this.$refs.hammer
                let mc = new Hammer.Manager(stage)

                let Rotate = new Hammer.Rotate()
                let Pinch = new Hammer.Pinch()

                mc.add([Pinch, Rotate])

                Pinch.recognizeWith(Rotate)

                if (this.rotatestart) {
                    mc.on('rotatestart', function (e) {
                        _this.rotatestart(e)
                    })
                }

                if (this.rotatemove) {
                    mc.on('rotatemove', function (e) {
                        _this.rotatemove(e)
                    })
                }

                if (this.rotateend) {
                    mc.on('rotateend', function (e) {
                        _this.rotateend(e)
                    })

                }

                if (this.pinchstart) {
                    mc.on('pinchstart', function (e) {
                        _this.pinchstart(e)
                    })
                }

                if (this.pinchmove) {
                    mc.on('pinchmove', function (e) {
                        _this.pinchmove(e)
                    })
                }

                if (this.pinchend) {
                    mc.on('pinchend', function (e) {
                        _this.pinchend(e)
                    })
                }
            },
        },
    }
</script>



