const state = {
  loadingState: false
}

// getters
const getters = {
    getState (state) {
        return state.loadingState
    }
}

// actions
const actions = {
    showLoading(context){
        context.commit('showLoading')
    },
    hideLoading(context){
        context.commit('hideLoading')
    },
}

// mutations
const mutations = {
    showLoading (state) {
        state.loadingState = true
    },

    hideLoading (state) {
        state.loadingState = false
    }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}


